import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { toBoolean } from 'igniteui-angular-core';

@Component({
  selector: 'app-pain-score',
  templateUrl: './pain-score.component.html',
  styleUrls: ['./pain-score.component.css']
})
export class PainScoreComponent implements OnInit {

  nsaidsChecked = false;
  braceChecked = false;
  injectionChecked = false;
  surgeryChecked = false;
  
  constructor(public dialogRef: MatDialogRef<PainScoreComponent>) {   
    this.nsaidsChecked = (localStorage.getItem("nsaids") == "true");
    this.braceChecked = (localStorage.getItem("brace") == "true");
    this.injectionChecked = (localStorage.getItem("injection") == "true");
    this.surgeryChecked = (localStorage.getItem("surgery") == "true");

  }

  ngOnInit(): void {
  }

  checkActivity(event: any): void {
    localStorage.setItem(event.source.id, event.checked);
  }
}
