export const builtInButtons = {
  cancel: {
    classes: 'cancel-button',
    secondary: true,
    text: 'Exit',
    type: 'cancel'
  },
  next: {
    classes: 'next-button',
    text: 'Next',
    type: 'next'
  },
  back: {
    classes: 'back-button',
    secondary: true,
    text: 'Back',
    type: 'back'
  }
};

export const defaultStepOptions = {
  classes: 'shadow-md bg-purple-dark',
  scrollTo: true,
  cancelIcon: {
    enabled: true
  }
};

export const steps = [
  {
    attachTo: {
      element: '.bar',
      on: 'right'
    },
    buttons: [
      builtInButtons.cancel,
      builtInButtons.next
    ],
    id: 'intro',
    arrow: true,
    title: 'Welcome to Rogveda!',
    text: `
          <p>
          This is your search bar
          </p>`
  },
  {
    attachTo: {
      element: '.filterBar',
      on: 'top'
    },
    buttons: [
      builtInButtons.cancel,
      builtInButtons.next
    ],
    id: 'filter',
    arrow: true,
    title: 'Use filters to get accurate treatment suggestions'
  },
  {
    attachTo: {
      element: '.searchResultsBar',
      on: 'bottom'
    },
    buttons: [
      builtInButtons.cancel,
      builtInButtons.next
    ],
    id: 'search',
    arrow: true,
    title: 'This is where you get treatment suggestions. They get more accurate with more filters'
  }
];