import { AfterViewInit, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { SearchPhraseSharingServiceService } from '../search-phrase-sharing-service.service';
import * as $ from 'jquery';
import { interval, Subscription } from 'rxjs';


@Component({
  selector: 'app-medical-search-page',
  templateUrl: './medical-search-page.component.html',
  styleUrls: ['./medical-search-page.component.css']
})


export class MedicalSearchPageComponent implements OnInit {

  constructor(private router: Router, private searchPhraseSharingServiceService: SearchPhraseSharingServiceService){
  }

  ngOnInit(): void {
    this.run();
    this.subscription = interval(40000).subscribe(val => this.run());
  }
  timeout_var = null;
  txt!: string;
  title = 'rogveda';
  subscription!: Subscription;
  searchPhrase: string ="";
  buttonValue1: string = "Pain in hand";
  buttonValue2: string = "Pain in hand while opening the lid of a jar";
  buttonValue3: string = "Pain near thumb";
  public goToSearchPage(){
    this.searchPhraseSharingServiceService.searchPhrase = this.searchPhrase;
    this.router.navigateByUrl("/searchResults");
  }

  public goToSearchPag(event: any){
    this.searchPhraseSharingServiceService.searchPhrase = event;
    this.router.navigateByUrl("/searchResults");
  }

  // Add something to given element placeholder
  public addToPlaceholder(toAdd: any, el: any) {
    el.attr('placeholder', el.attr('placeholder') + toAdd);
    // Delay between symbols "typing" 
    return new Promise(resolve => setTimeout(resolve, 100));
}

// Cleare placeholder attribute in given element
  public clearPlaceholder(el: any) {
    el.attr("placeholder", "");
}

// Print one phrase
  public printPhrase(phrase: string, el: any) {
    return new Promise(resolve => {
        // Clear placeholder before typing next phrase
        this.clearPlaceholder(el);
        let letters = phrase.split('');
        // For each letter in phrase
        letters.reduce(
            (promise, letter, index) => promise.then(_ => {
                // Resolve promise when all letters are typed
                if (index === letters.length - 1) {
                    // Delay before start next phrase "typing"
                    setTimeout(resolve, 1000);
                }
                el.attr('placeholder', el.attr('placeholder') + letter);
                // Delay between symbols "typing" 
                return new Promise(resolve => setTimeout(resolve, 100));
            }),
            Promise.resolve()
        );
    });
} 

// Print given phrases to element
public printPhrases(phrases: any[], el: any) {
    // For each phrase
    // wait for phrase to be typed
    // before start typing next
    this.clearPlaceholder(el);
    phrases.reduce(
        (promise, phrase) => promise.then((_: any) => this.printPhrase(phrase, el)), 
        Promise.resolve()
    );
}

// Start typing
public run() {
  this.clearPlaceholder($('#searchbox'));
    let phrases = [
        //"Search Website e.g. \"Dancing Cats\"",
        "Pain in wrist after pregnancy",
        "Wrist pain diy treatment",
        "Wrist pain treatment",
        "Wrist pain thumb side treatment",
        "Wrist pain treatment in ayurveda"
    ];

    this.printPhrases(phrases, $('#searchbox'));
}

}
