import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class SearchPhraseSharingServiceService {

  _searchPhrase!: string;
  constructor() { }

  set searchPhrase(searchPhrase: string){
    this._searchPhrase=searchPhrase;
  }

  get searchPhrase() {
    return this._searchPhrase;
  }
}
