import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { MedicalSearchResultsComponent } from './medical-search-results/medical-search-results.component';
import { MedicalSearchPageComponent } from './medical-search-page/medical-search-page.component';

const routes: Routes = [
  { path: '', component: MedicalSearchPageComponent },
  { path: 'searchResults', component: MedicalSearchResultsComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { 

}
